/**
 * Vue
 * this.xxx
 */
import Utils from '@/utils/utils'

// 
let timer = null
function debounce (fn, delay = 500) {
  clearTimeout(timer)
  return function () {
    let context = this
    let args = arguments
    clearTimeout(timer)
    timer = setTimeout(() => {
      fn.apply(context, args)
    }, delay)
  }
}

const base = {
  install (Vue, options) {
    Vue.prototype.openLoading = Utils.openLoading
    Vue.prototype.closeLoading = Utils.closeLoading
    Vue.prototype.debounce = debounce
  }
}

export default base
